  <div class="kompetence">
    <h3>Kompetence</h3>

    <table id="kompetenceTable">
      <tr>
        <th>Kompetence: </th>
        <th>Samoprocena</th>
      </tr>
      <tr v-for="(komp,i) in kompetence" v-bind:key="i">
        <td>{{komp.name}}</td>
        <td>
          <select name="type" id="type" v-model="komp.samoProcena" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
    </table>

  </div>

  <div class="licni">
    <h3>Licni KPI</h3>

    <table id="LicniTable">
      <tr>
        <th>Licni KPI: </th>
        <th>Samoprocena</th>
      </tr>
      <tr v-for="(komp,i) in licniKpi" v-bind:key="i">
        <td>{{komp.name}}</td>
        <td>
          <select name="type" id="type" v-model="komp.samoProcena" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
    </table>

  </div>

  <div class="licni">
    <h3>Kompanijski KPI</h3>

    <table id="LicniTable">
      <tr>
        <th>Kompanijski KPI: </th>
        <th>Samoprocena</th>
      </tr>
      <tr v-for="(komp,i) in kompanijskiKpi" v-bind:key="i">
        <td>{{komp.name}}</td>
        <td>
          <select name="type" id="type" v-model="komp.samoProcena" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
    </table>

  </div>

  <div class="komentar">
    <h4>Komentar</h4>
    <input
      class="form-control"
      type="text"
      placeholder=""
      name="komentar"
      v-model="komentar"
      id="komentar"
      >
  </div>

  <div class="submitDiv">
  <button class="btn btn-primary" @click="submit">Submit</button>
  </div>


</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';

export default {
    computed: mapGetters(['user','companys','usersSameCompany']),
    data(){
      return{
        odgovori:[1,2,3,4],
        komentar:"",
        kompetence:[],
        kompanijskiKpi:[],
        licniKpi:[],
        collegue:"",
      }
    },
    methods:{
        ...mapActions(['getProfile','getCompanies','getCollegues','setLicniKpiSamoProcena']),
        submit(){
          let tempNadredjeni;
          for (var i = 0; i < this.usersSameCompany.length; i++) {
            if(this.collegue==this.usersSameCompany[i].name){
              tempNadredjeni=this.usersSameCompany[i];
            }
          }
          let kpiData={
            userId:this.user._id,
            licniKpi:this.licniKpi,
            kompanijski: this.kompanijskiKpi,
            kompetence:this.kompetence,
            nadredjeni:tempNadredjeni,
          }
          this.setLicniKpiSamoProcena(kpiData).then(res=>{
            if(res.data.success){
              this.$router.push('/profile');
            }
          }).catch(err =>{
            console.log(err);
          });
        },
      },
    async created(){
      await this.getProfile();
      await this.getCompanies();
      await this.getCollegues(this.user.type);
      let tempCompany;
      for (var i = 0; i < this.companys.length; i++) {
          if(this.user.type == this.companys[i].name){
            tempCompany = this.companys[i];
          }
      }
      this.kompetence=tempCompany.performanceEvaluation.kompetence;
      for ( i = 0; i < this.kompetence.length; i++) {
        this.kompetence[i].samoProcena=1;
      }
      this.kompanijskiKpi=tempCompany.performanceEvaluation.kompanijski;
      for ( i = 0; i < this.kompanijskiKpi.length; i++) {
        this.kompanijskiKpi[i].samoProcena=1;
      }
      this.licniKpi=this.user.performanceEvaluation.licniKpi;
    },
}
</script>

